import TitleCard from "../../../components/Cards/TitleCard";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { fetchAllMemberAction } from "../../../app/service/action/memberAction";
import Loading from "../../../components/Loading/Loading";
import { Link } from "react-router-dom";
import { EyeIcon, TrashIcon } from "@heroicons/react/24/outline";
import { PaginationComponent } from "../../../components/Pagination/PaginationComponent";
import { StatusMember } from "../../../components/Typography/StatusMember";
import SearchBar from "../../../components/Input/SearchBar";
import {sendToWA} from "../../../utils/Utils";
import {WhatsApp} from "@mui/icons-material";

export const Regular = () => {
    const dispatch = useDispatch()
    const [page, setPage] = useState(1);
    const [search, setSearch] = useState('');

    const data = useSelector(({ membersState }) => membersState.members.data)
    const isLoading = useSelector(({ membersState }) => membersState.members.isLoading)

    useEffect(() => {
        dispatch(fetchAllMemberAction(page, search)).then().catch(error => {
            console.log(error)
        })
    }, [dispatch, page, search])


    const handleChange = (event, value) => {
        setPage(value);
        // @ts-ignore
        dispatch(fetchAllMemberAction(value, search)).then();
    };

    return (
        <>
            <TitleCard title={"Data Member"} topMargin={'mt-2'}
                TopSideButtons={<SearchBar placeholderText={'Cari Nama Rider'}
                    setSearchText={(input) => setSearch(input)} />}>
                {isLoading ? <Loading /> :
                    <div className="overflow-x-auto w-full">
                        <table className="table table-zebra w-full">
                            <thead>
                                <tr>
                                    <th>No</th>
                                    <th>Tanggal</th>
                                    <th>Data Rider</th>
                                    <th>Email</th>
                                    <th>Data Orang Tua</th>
                                    <th>Alamat</th>
                                    <th>Status</th>
                                    <th></th>
                                </tr>
                            </thead>
                            <tbody>
                                {
                                    data.result ? data.result.map((value, k) => {
                                        return (
                                            <tr key={k}>
                                                <td>{k + 1}</td>
                                                {/*<td>{moment(new Date()).add(-5 * (k + 2), 'days').format("DD MMM YY")}</td>*/}
                                                <td>{value.createdAt}</td>
                                                <td>
                                                    <div className="flex items-center space-x-3">
                                                        <div>
                                                            <div className="font-bold">{value.fullname}</div>
                                                            <div className="text-sm opacity-50">{value.nickname}</div>
                                                            <div className="text-sm opacity-50">{value.categoryRace}</div>
                                                            <div className="text-sm opacity-50">{value.memberId}</div>
                                                        </div>
                                                    </div>
                                                </td>
                                                <td>{value.email}</td>
                                                <td>
                                                    <div
                                                        className="text-sm">
                                                        Ayah: {value.fatherName} <br/>
                                                        HP:
                                                        <a href={sendToWA(value.fatherPhone)} target="_blank"
                                                           rel="noreferrer">
                                                            <WhatsApp className="w-5"
                                                                      color="green"/> {value.fatherPhone}</a>
                                                    </div>
                                                    <div className="text-sm"> Ibu: {value.motherName} <br/>
                                                        HP:
                                                        <a href={sendToWA(value.motherPhone)} target="_blank"
                                                           rel="noreferrer">
                                                            <WhatsApp className="w-5"
                                                                      color="green"/> {value.motherPhone} </a>
                                                    </div>
                                                </td>
                                                <td>{value.address}</td>
                                                <td>{StatusMember(value.status)}</td>
                                                <td>
                                                    <Link to={`${value.id}/update`}>
                                                        <button className="btn btn-square btn-ghost">
                                                            <EyeIcon className="w-5" color={'blue'}/>
                                                        </button>
                                                    </Link>
                                                    <button className="btn btn-square btn-ghost">
                                                        <TrashIcon className="w-5" color={'red'}/>
                                                    </button>
                                                </td>
                                            </tr>
                                        )
                                    }) : (
                                        <tr>
                                            <td colSpan={7} align={'center'}>DATA TIDAK DITEMUKAN</td>
                                        </tr>
                                    )
                                }
                            </tbody>
                        </table>
                        <PaginationComponent data={data} handleChange={handleChange} page={page} />
                    </div>
                }
            </TitleCard>
        </>
    )
};

